import { RecordType, ResourceType } from 'react-restful';

import { City } from './city';

export interface County extends RecordType {
    readonly id?: string;
    readonly name: string;
}

export const countyResourceType = new ResourceType<County>({
    name: "County",
    schema: [{
        field: 'id',
        type: 'PK'
    }, {
        field: "counties",
        resourceType: "City",
        type: 'FK'
    }]
});